import "./menu.css";
import { useEffect, useState } from 'react';
import { NavLink } from "react-router-dom";
import { store } from "../../../redux/store";

function Menu(): JSX.Element {
    const [userType, setUserType] = useState('');

    const adminMenu = ()=>{
        return (
            <>
                 <br/>
                <NavLink to="admin/addCompany"> </NavLink><br/>
                <NavLink to="admin/addCustomer"> </NavLink><br/>
                {/**
                <NavLink to="admin/deleteCompany"> </NavLink><br/>
                <NavLink to="admin/deleteCustomer"> </NavLink><br/>
                 */}
                <NavLink to="admin/getAllCompanies"> </NavLink><br/>
                <NavLink to="admin/getAllCustomers"> </NavLink><br/>
                <NavLink to="admin/getCustomer"> </NavLink><br/>
                <NavLink to="admin/getOneCompany"> </NavLink><br/>
                {/**
                <NavLink to="admin/updateCompany"> </NavLink><br/>
                <NavLink to="admin/updateCustomer"> </NavLink><br/>   
                 */}  
                <br/>
            </>
        );
    };
   
    const companyMenu = ()=>{
        return (
            <>
                 <br/>
                <NavLink to="company/addCoupon"> </NavLink><br/>
                <NavLink to="company/deleteCoupon"> </NavLink><br/>
                <NavLink to="company/getAllCompanyCoupons"> </NavLink><br/>
                <NavLink to="company/getCompanyDetails"> </NavLink><br/>
                <NavLink to="company/getCouponsByCategory">  </NavLink><br/>
                <NavLink to="company/getCouponsByMaxPrice">  </NavLink><br/>
                {//<NavLink to="company/updateCoupon"> </NavLink><br/>
                }
                <br/>        
            </>
        ); 
    };

    const customerMenu = ()=>{
        return (
            <>
                <br/>
                <NavLink to="customer/getCustomerCoupons"> </NavLink><br/>
                <NavLink to="customer/getCustomerCouponsByCategory">  </NavLink><br/>
                <NavLink to="customer/getCustomerCouponsByMaxPrice">  </NavLink><br/>
                <NavLink to="customer/getCustomerDetails"> </NavLink><br/>
                {/*<NavLink to="customer/purchaseCoupon"> </NavLink><br/>*/}
            </>
        )
    }

    const display = () => {
        if(store.getState().authState.userType == "ADMIN"){
            return adminMenu();
        }
        if(store.getState().authState.userType == "COMPANY"){
            return companyMenu();
        }
        if(store.getState().authState.userType == "CUSTOMER"){
            return customerMenu();
        }
    };

    useEffect(()=>{
        display();
    },[userType])
    store.subscribe(()=>{
        if(store.getState().authState.userType !== userType){
            setUserType(store.getState().authState.userType);
        }
    })
    return (
        <div className="menu">           
            {display()} <br/>
            <NavLink to="/allCoupons"> </NavLink>       
        </div>
    );
}

export default Menu;
